.news-detail {
  .title {
    margin-left: auto;
    margin-right: auto;
  }
}

.news-detail__container {
  padding: 0;
  margin: 0;
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 1120px;
    margin: 0 auto;
    padding-left: 110px;
    padding-right: 103px;
  }
}

.news-detail__title {
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 10px;
    width: 708px;
  }
}

.news-detail__date {
  font-size: 12px;
  line-height: 24px;
  margin-right: 17px;


  color: #fff;
  padding: 1px 7px;
  background-image: url(../img/bg-data.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 90px;
  height: 26px;
}

.news-detail__data {
  padding: 0 20px;
  display: flex;
  justify-content: center;

  @media (min-width: $tablet-width) {
    display: inline-flex;
  }
}

.news-detail__author {
  font-size: 12px;
  line-height: 24px;
}

.news-detail__tags {
  @include ul-reset;
  font-size: 12px;
  line-height: 24px;
  display: flex;
  padding: 0 20px;
  justify-content: center;
  margin-bottom: 30px;

  li:not(:last-child) {
    margin-right: 10px;
  }

  @media (min-width: $tablet-width) {
    display: inline-flex;
  }
}

.news-detail__text {
  p {
    margin: 5px 0;
  }

  @media (min-width: $tablet-width) {
    width: 446px;
  }

  @media (min-width: $desktop-width) {
    width: 630px;
  }
}

.news-detail__img {
  display: block;

  @media (min-width: $tablet-width) {
    width: 100%;
  }
}

.news-detail__content {
  display: flex;
  flex-direction: column;
  padding: 25px 20px 0;

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    width: 708px;
    justify-content: space-between;
    flex-direction: row;
    padding: 30px 10px 0;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
}

.news-detail__reference {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    width: 100%;
    justify-content: center;
  }
}
