* {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: $font-family-primary;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  background: $background-color-fourth;
  overflow-x: hidden;
  color: $color-secondary;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  @include visually-hidden;
}

.container {
  display: block;
  //width: 320px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 708px;
    padding: 0 20px;
  }

  @media (min-width: $desktop-width) {
    width: 1120px;
    padding: 0 20px;
    margin: 0 auto;
  }
}


html, body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
}
.page-main{
  flex: 1 0 auto;
}
.page-footer {
  flex-shrink: 0;
}


a {
  color: #4a82c4;
  text-decoration: none;
  position: relative;

  &::after {
    @include pseudo;
    @include size(100%, 1px);
    bottom: 0;
    left: 0;
    background-color: #4a82c4;
  }
}
